<template>
	<scroll-view
	@refresherpulling="onpulling"
	@refresherrefresh="onRefresh"
	@refresherrestore="onRestore"
	@refresherabort="onRabort"
	class="view-scroll-view"
	:scroll-x="scrollX"
	:scroll-y="scrollY"	
	:upper-threshold="upperThreshold"
	:ower-threshold="owerThreshold"
	:scroll-top="scrollTop"
	:scroll-left="scrollLeft"
	:scroll-into-view="scrollIntoView"
	:scroll-with-animation="scrollWithAnimation"
	:enable-back-to-top="enableBackToTop"
	:show-scrollbar="showScrollbar"
	:refresher-enabled="refresherEnabled"
	:refresher-threshold="refresherThreshold"
	:refresher-default-style="refresherDefaultStyle"
	:refresher-background="refresherBackground"
	:refresher-triggered="refresherTriggered"
	:enable-flex="enableFlex"
	:scroll-anchoring="scrollAnchoring"
	id="scrop-view"
	:style="{
		height:height
	}"
	>
		<template>
			<view class="view-scroll_contexnt" ref="viewScroll"
				:style="{
					top:y+'px',
					show:isShow
				}"
			>
				<slot name="icon"></slot>
			</view>
		</template>

		<slot></slot>

	</scroll-view>
</template>

<script>
	export default {
		props:{
			"scroll-x":{
				type:Boolean,
				default:false
			},
			"scroll-y":{
				type:Boolean,
				default:false
			},
			"upper-threshold":{
				type:Number|String,
				default:50
			},
			"ower-threshold":{
				type:Number|String,
				default:50
			},
			"scroll-top":{
				type:Number|String,
				default:""
			},
			"scroll-left":{
				type:Number|String,
				default:""
			},
			"scroll-into-view":{
				type:String,
				default:""
			},
			"scroll-with-animation":{
				type:Boolean,
				default:false
			},
			"enable-back-to-top":{
				type:Boolean,
				default:false
			},
			"show-scrollbar":{
				type:Boolean,
				default:false
			},
			"refresher-enabled":{
				type:Boolean,
				default:false
			},
			"refresher-threshold":{
				type:Number,
				default:45
			},
			"refresher-default-style":{
				type:String,
				default:"black"
			},
			"refresher-background":{
				type:String,
				default:"#Fff"
			},
			"refresher-triggered":{
				type:Boolean | String,
				default:false
			},
			"enable-flex":{
				type:Boolean,
				default:false
			},
			"scroll-anchoring":{
				type:Boolean,
				default:false
			},
			offsetY:{
				type:Number | String,
				default:null
			},
			height:{
				type:String,
				default:"100%"
			}
		},
		data(){
			return {
				y:0,
				isShow:false
			}
		},
		created(){
			// #ifdef MP-WEIXIN
				this.y = this.offsetY;
			// #endif
		},
		methods:{
			// 自定义下拉控制被下拉
			onpulling(e){	
				// #ifdef H5
					let y = e.detail.deltaY
					this.y = y;
				// #endif
				
				this.$emit("refresherpulling",e)
				
				this.isShow = true
			},
			// 自定义下拉刷新被触发
			onRefresh(e){
				this.$emit("refresherrefresh",e)
				// #ifdef H5
					this.y = this.offsetY;
				// #endif
				
			},
			// 复位
			 onRestore(e){
				// #ifdef H5
				 	this.y = 0;
				// #endif
		
				this.$emit("refresherrestore",e);
								
				this.isShow = false
				
			 },
			 // 中止
			 onRabort(e){
				 this.$emit("refresherabort",e)
				  // #ifdef H5
				  	this.y = 0;
				 // #endif
				 this.isShow = false
			 }
		},
		watch:{
			"scrollIntoView"(newValue){
				console.log(`666`,newValue)
			}
		}
	}
	
	
</script>

<style lang="scss">
	.view-scroll-view{
		position:relative;
		width: 100%;
		height: 100%;
		/* #ifdef MP-WEIXIN  */ 
			height: calc(100vh - 74px)
		/* #endif  */
		
	}
	.view-scroll_contexnt{
		position: fixed;
		left: 50%;
		transform: translatex(-50%);
		z-index:1;
		display: flex;
		align-items: center;
	}
</style>
